import React from 'react';
import {OrbitControls, PerspectiveCamera} from "@react-three/drei";
import Ground from "./Ground";

const CarShow = () => {
  return (
    <>
      <OrbitControls autoRotate={true} position={[0,0,0]} target={[0, 0.35, 0]} maxPolarAngle={1.45}/>
      <PerspectiveCamera makeDefault={true} fov={50} position={[3,5,15]}/>
      <color args={[0,0,0]} attach='background'/>
      <spotLight
        color={[1,0.25, 0.7]}
        intensity={3}
        angle={.6}
        penumbra={.5}
        position={[5,10,0]}
        castShadow={true}
        shadow-bias={-0.0001}
      />

      <spotLight
        color={[0.14,0.5, 1]}
        intensity={2}
        angle={.6}
        penumbra={.5}
        position={[-5,10,0]}
        castShadow={true}
        shadow-bias={-0.0001}
      />
      <Ground/>
    </>
  );
};

export default CarShow;
